
//
@mixin hide-text {
    text-indent: 101%;
    /*  */
    overflow: hidden;
    /*  */
    white-space: nowrap;
}

//()
@mixin ellipsis($rows) {
    display: -webkit-box;
    -webkit-line-clamp: $rows;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

//position-absolute center
@mixin position-absolute-center-center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}